	module.exports = {"data":{"markdownRemark":{"html":"<p>F Junが来て、記事を \"Wang Huning against Wang Huning\"に持ってきました。私の内部を少し理解しているようです。彼女は材料を手伝って欲しいと言ったが、私はそれらを提供できないと言った。この地域には道徳的な問題があるので、私はそのような記事のキャンプに参加できない。これは一貫した人生の原則です。しかし、F Junのこのことは、普通の人が理解しにくい絵を書くようなものです。 Wenは言った：Wang Huningは同質の全体ではないかもしれないが、全体的に変化する彼は絶えず革新し、彼自身を排除する。彼はまた、彼は非常に合理的な人であり、彼はいつも非常に合理的であり、感情的ではないと言った。それは？</p>\n<p>この世に住む人々：弱い人もあれば強い人も、強くなる人もいますし、他人に目標を設定したり、目標を設定したり、人生の感情的なサポートが必要な人、人生をサポートする必要がある人もいます。私はたぶんあらゆる概念のコンセプトの中で後者を選ぶだろう。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0008.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0008.md","frontmatter":{"title":"1月11日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0008.md absPath of file >>> MarkdownRemark"}}

